import {
    createRouter,
    createWebHashHistory
} from "vue-router"
const routes = [

    {

        path: '/',

        component: () => import('@/components/floor3d'),
        children: [{

            path: 'clocks',

            name: "clocks",

            component: () => import('../components/clocks.vue')

        }, ],
    },


]

export const router = createRouter({

    history: createWebHashHistory(),

    routes,

})

export default router