export default function () {
    //创建画布
    let canvas = document.createElement("canvas");
    let ctx = canvas.getContext("2d");
    window.document.body.appendChild(canvas);
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    canvas.setAttribute(
        "style",
        "position:fixed;left:0;top:0;pointer-events:none;filter:blur(1px);"
    );
    //初始化参数
    let clicks = [];
    let points = [];
    let colors = [
        "255, 127, 80",
        "83, 82, 237",
        "30, 144, 255",
        "46, 213, 115",
        "223, 228, 234",
        "255, 165, 2",
    ];
    let live = 30;
    //监听事件
    window.addEventListener("mousemove", function (e) {
        //i 为粒子数量
        for (let i = 0; i < 15; i++) {
            points.push({
                sx: e.x,
                sy: e.y,
                vx: 0.5 - Math.random(),
                vy: 0.5 - Math.random(),
                life: live,
                color: colors[parseInt(Math.random() * colors.length)],
                size: Math.random() * 5,
            });
        }
    });
    window.addEventListener("click", function (e) {
        //j 为点击产生的粒子数量
        for (let i = 0; i < 50; i++) {
            clicks.push({
                sx: e.x,
                sy: e.y,
                vx: 0.5 - Math.random(),
                vy: 0.5 - Math.random(),
                //元素存活时间
                life: live,
                //随机颜色
                color: colors[parseInt(Math.random() * colors.length)],
                //随机大小
                size: Math.random() * 5,
            });
        }
    });
    //绘图函数，用于在画布上绘制跟随的图案
    function draw() {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        for (var i = 0; i < points.length; i++) {
            var point = points[i];
            ctx.beginPath();
            //根据起点坐标和尺寸绘制圆形
            ctx.arc(point.sx, point.sy, point.size, Math.PI * 2, false);
            //填充颜色，并根据存活值改变透明度
            ctx.fillStyle = "rgb(" + point.color + "," + point.life / live + ")";
            ctx.fill();
            //每次绘制都会减小元素存活值，直到存活值归0
            point.life--;
            if (point.life <= 0) {
                //将归零的元素从数组中删除
                points.splice(i, 1);
            }
            //根据移动方向调整下次绘制位置
            point.sx += point.vx * 3;
            point.sy += point.vy * 3;
            point.vy += 0.03;
        }
        for (var j = 0; j < clicks.length; j++) {
            var click = clicks[j];

            ctx.fillStyle = "rgb(" + click.color + "," + click.life / live + ")";
            ctx.fillRect(click.sx, click.sy, 3, 3);
            ctx.fill();
            click.life--;
            if (click.life <= 0) {
                clicks.splice(j, 1);
            }
            click.sx += click.vx * 5;
            click.sy += click.vy * 5;
            click.vy += 0.03;
        }
        requestAnimationFrame(draw)
    }
    draw()
}